.skillsBlock {
    min-height: 100vh;
    background-color: rgb(188, 216, 138);
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
    border: 1px solid;
}

.title {
    background-color: rgb(118, 121, 115);
}

.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
